





























footer {
  border-top: 1px solid #e7e7e7;
  .footer__inner {
    display: flex;
    align-items: center;
    margin: 0 20px 1px;
    min-height: 42px;
    justify-content: space-between;
    color: #a0a0a0;
    .home-link {
      line-height: 0;
      font-size: 1.3rem;
      color: #868686;
      font-weight: bold;
      transition: .2s;
      &:hover {
        color: #333;
      }
    }
  }
}

footer.white {
  .home-link {
    color: rgba(255, 255, 255, .8);
    &:hover {
      color: #fff;
    }
  }
}
